html,
body {
  height: 100%;
  margin: 0px;
  margin-bottom: 20px;
}

.App {
  text-align: center;
  /*margin-top: 50px;*/
  background-color: #d29f7c; /* set the background color to a light brown */
  min-height: 100%;
  flex-direction: column;
  margin-bottom: 20px;
}

.App-images {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  height: 100px;
  padding-top: 220px;
}

.App-images img {
  max-width: 100%;
  height: auto;
}

@media only screen and (max-width: 800px) {
  .App-images {
    width: 100%;
  }
}

@media only screen and (max-width: 440px) {
  .App-images {
    width: 100%;
    padding-top: 140px;
  }
}

.App-header {
  background-color: #473729; /* set the header background color to a darker brown */
  height: 180px;
  padding: 0px;
  /*white-space: nowrap;*/
  overflow: hidden;
  color: white;
  animation-name: header-fade-in; /* add an animation to fade in the header */
  animation-duration: 4s;
  animation-fill-mode: forwards;
}

@keyframes header-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media only screen and (max-width: 800px) {
  .App-header {
    background-color: #473729; /* set the header background color to a darker brown */
    height: 155px;
    padding: 0px;
    /*white-space: nowrap;*/
    overflow: hidden;
    color: white;
    animation-name: header-fade-in; /* add an animation to fade in the header */
    animation-duration: 4s;
    animation-fill-mode: forwards;
  }
}

.App-title {
  font-size: 55px;
  font-family: Garamond, Rockwell;
  margin-bottom: 0; /* remove margin bottom to align with intro text */
}
@media only screen and (max-width: 800px) {
  .App-title {
    font-size: 40px;
    font-family: Garamond, Rockwell;
    margin-bottom: 0;
  }
}

.App-intro {
  overflow: hidden;
  font-size: 40px;
  width: 80%;
  font-family: Garamond, Rockwell;
  text-align: center;
  margin: auto;
  font-family: Garamond, Rockwell;
  font-size: 30px;
  margin-top: 20px;
  line-height: 1.5;
  /* Ensures the content is not revealed until the animation */
  border-right: 0.15em solid black; /* The typewriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  letter-spacing: 0.15em; /* Adjust as needed */
  animation: header-fade-in 4s forwards, typing-1 4s steps(40, end) 1s infinite,
    typing-2 4s steps(40, end) 4s infinite,
    typing-3 4s steps(40, end) 7s infinite, stop-cursor 0s 9s forwards,
    blink-caret 0.75s step-end infinite;
}
@keyframes header-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media only screen and (max-width: 800px) {
  .App-intro {
    overflow: hidden;
    font-size: 40px;
    width: 80%;
    font-family: Garamond, Rockwell;
    text-align: center;
    margin: auto;
    font-family: Garamond, Rockwell;
    font-size: 20px;
    margin-top: 20px;
    line-height: 1.5;
    /* Ensures the content is not revealed until the animation */
    border-right: 0.15em solid black; /* The typewriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    letter-spacing: 0.001em; /* Adjust as needed */
    animation: header-fade-in 4s forwards,
      typing-1 4s steps(40, end) 1s infinite,
      typing-2 4s steps(40, end) 4s infinite,
      typing-3 4s steps(40, end) 7s infinite, stop-cursor 0s 9s forwards,
      blink-caret 0.75s step-end infinite;
  }
}

@media only screen and (max-width: 450px) {
  .App-intro {
    overflow: hidden;
    font-size: 40px;
    width: 80%;
    font-family: Garamond, Rockwell;
    text-align: center;
    margin: auto;
    font-family: Garamond, Rockwell;
    font-size: 15px;
    margin-top: 20px;
    line-height: 1.5;
    /* Ensures the content is not revealed until the animation */
    border-right: 0.15em solid black; /* The typewriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    letter-spacing: 0.0001em; /* Adjust as needed */
    animation: header-fade-in 4s forwards,
      typing-1 4s steps(40, end) 1s infinite,
      typing-2 4s steps(40, end) 4s infinite,
      typing-3 4s steps(40, end) 7s infinite, stop-cursor 0s 9s forwards,
      blink-caret 0.75s step-end infinite;
  }
}

/* Animation for the first sentence */
@keyframes typing-1 {
  0% {
    width: 0;
  }
  20% {
    width: 20%;
  }
  40% {
    width: 40%;
  }
  60% {
    width: 60%;
  }
  80% {
    width: 80%;
  }
  100% {
    width: 100%;
  }
}

/* Animation for the second sentence */
@keyframes typing-2 {
  0% {
    width: 0;
  }
  10% {
    width: 10%;
  }
  20% {
    width: 20%;
  }
  30% {
    width: 30%;
  }
  40% {
    width: 40%;
  }
  50% {
    width: 50%;
  }
  60% {
    width: 60%;
  }
  70% {
    width: 70%;
  }
  80% {
    width: 80%;
  }
  90% {
    width: 90%;
  }
  100% {
    width: 100%;
  }
}

/* Animation for the third sentence */
@keyframes typing-3 {
  0% {
    width: 0;
  }
  20% {
    width: 20%;
  }
  40% {
    width: 40%;
  }
  60% {
    width: 60%;
  }
  80% {
    width: 80%;
  }
  100% {
    width: 100%;
  }
}

/* The typewriter cursor animation */
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: black;
  }
}

@keyframes stop-cursor {
  to {
    border-right-width: 100;
  }
}

h2 {
  font-family: Garamond, Rockwell;
  font-size: 45px;
  margin-top: 40px;
  animation: shake 10s infinite;
  animation-iteration-count: infinite;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }
  10% {
    transform: translateX(-10px);
  }
  20% {
    transform: translateX(10px);
  }
  30% {
    transform: translateX(-10px);
  }
  40% {
    transform: translateX(10px);
  }
  50% {
    transform: translateX(-10px);
  }
  60% {
    transform: translateX(10px);
  }
  70% {
    transform: translateX(-10px);
  }
  80% {
    transform: translateX(10px);
  }
}

h3 {
  font-family: Garamond, Rockwell;
  font-size: 26px;
  margin-top: 30px;
}

